import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const views = [
  { name: 'main', title: '主页面' },
  { name: 'main2', title: '主页面' },
  ]

const basicMap = () => views.map(it => (
  { path: '/' + it.name, component: () => import('../views/' + it.name + '/index'),
    hidden: true,
    meta: { title: it.title}
  }
))

export default new Router({
  routes: [ ...basicMap(), { path: '/', redirect: '/main2' } ]
})



